<template>
    <section id="contactMe">
        <div class="container">
            <h2 class="text-center">Contact with me</h2>
            <div class="row">
                <div class="col-log-12">
                    <ul class="d-flex justify-content-center">
                        <li class="instagram animated">
                            <a href="https://www.instagram.com/koryun__001/" target="_blank">
                                <img src="@/assets/instagram.png" alt="instagram">
                            </a>
                        </li>
                        <li class="facebook animated">
                            <a href="https://www.facebook.com/people/Koryun-Avetisyan/100006426123378/" target="_blank">
                                <img src="@/assets/facebook.png" alt="facebook">
                            </a>
                        </li>
                        <li class="github animated">
                            <a href="https://github.com/KoryunAvetisyan " target="_blank">
                                <img src="@/assets/github.png" alt="github">
                            </a>
                        </li>
                        <li class="linkedin animated">
                            <a href="https://www.linkedin.com/in/koryun-avetisyan-729045213/" target="_blank">
                                <img src="@/assets/linkedin.png" alt="linkedin">
                            </a>
                        </li>
                    </ul>
                    <p class="text-center">Send me your message or suggestion. I will look at it with love.</p>
                    <div class="row">
                        <div class="col-md-12">
                            <form id="myForm">
                                <div class="input-group mb-3">
                                    <input type="text" class="form-control shadow-none" placeholder="Name" v-model="name" id="name">
                                </div>
                                <div class="input-group mb-3">
                                    <input type="email" class="form-control shadow-none" placeholder="Email" v-model="email" name="email">
                                </div>
                                <div class="input-group mb-3">
                                    <textarea class="form-control shadow-none" rows="5" placeholder="Message" v-model="message" name="message"></textarea>
                                </div>
                                <div class="input-group mb-3 d-flex justify-content-end">
                                    <button type="button" class="send-button" @click="sendEmail()">Send</button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import emailjs from 'emailjs-com'
export default {
    name: 'ContactView',
    data() {
        return {
            name: '',
            email: '',
            message: '',
            serviceID: 'service_j45kydp',
            templateID: 'template_ls2hp2l',
            userID: 'BFcdt_tktmRDSkI6A'
        }
    },
    mounted() {
        emailjs.init(this.userID);
    },
    methods: {
        sendEmail() {
            if(this.name != '' && this.email != '' && this.message != '') {
                try {
                    emailjs.sendForm(this.serviceID, this.templateID, "#myForm", this.userID)
                } catch(error) {
                    console.log({error})
                }
                this.name = ''
                this.email = ''
                this.message = ''
            }
        },
    }
}
</script>

<style scoped>
#contactMe {
    padding: 30px 0 30px;
    color: #fff;
}
#contactMe h2 {
    color: #FF4A57;
}
ul {
    margin: 0;
    padding: 0;
}
ul li {
    list-style: none;
    width: 55px;
    margin: 5px;
    height: 50px;
    padding: 10px;
    border-radius: 3px;
}
ul li:hover {
    transition: 0.3s;
    transform: scale(1.1);
}
#myForm input {
    background-color: #212336;
    color: #fff;
    border: none;
}
#myForm textarea {
    background-color: #212336;
    color: #fff;
    border: none;
}
#myForm input::placeholder, #myForm textarea::placeholder {
    color: #fff;
    opacity: 0.5;
}
.send-button {
    background-color: #FF4A57;
    border: none;
    border-radius: 5px;
    padding: 7px;
    color: #fff;
}
.animated {
    animation-name: animateIcons;
    animation-duration: 1.5s;
}
@keyframes animateIcons {
    0%   {transform: scale(0.2);}
    50%  {transform: scale(1.1);}
    100% {transform: scale(1);}
}
</style>