<template>
    <nav class="navbar navbar-expand navbar-light fixed top_navbar">
        <div class="mx-auto">
            <ul id="navbar" class="navbar-nav mt-2">
                <li class="nav-item" :class="{ active: path == '/' }">
                    <router-link to="/">Home</router-link>
                </li>
                <li class="nav-item" :class="{ active: path == '/about' }">
                    <router-link to="/about">About</router-link>
                </li> 
                <li class="nav-item" :class="{ active: path == '/contact' }">
                    <router-link to="/contact">Contact</router-link>
                </li>
            </ul>
        </div>
    </nav>
</template>

<script>
export default {
    name: 'NavbarComponent',
    data() {
        return {
            path: this.$route.path
        }
    },  
    watch: {
        $route(val) {
            this.path = val.path;
        }
    }
}
</script>

<style scoped>
.top_navbar {
    background-color: #212336;
}
#navbar li {
    padding: 10px;
    cursor: pointer;
    font-weight: bold;
}
#navbar li a {
    color: #fff;
    text-decoration: none;
}
.active a {
    color: #FF4A57 !important;
}
#navbar li a:hover {
    color: #FF4A57;
    transition: 0.2s;
}
</style>